import { AuthProvider } from 'react-admin';
import dataProvider from './dataProvider';

async function loginAPI(id: string, password: string) {
    const { data: response } = await dataProvider.create('login', {
        data: {
            id,
            password,
        },
    });

    return response;
}

const authProvider: AuthProvider = {
    login: async ({ id, password }) => {
        const response = await loginAPI(id, password);
        // if (response?.success && response?.data?.token) {
        //     const { token } = response.data;
        //     sessionStorage.setItem('token', token);
        //     return Promise.resolve();
        // }
        // return Promise.reject();
        return Promise.resolve();
    },
    logout: () => {
        sessionStorage.removeItem('token');
        return Promise.resolve();
    },
    checkError: () => Promise.resolve(),
    checkAuth: () =>
        // sessionStorage.getItem('token') ? Promise.resolve() : Promise.reject(),
        Promise.resolve(),
    // eslint-disable-next-line prefer-promise-reject-errors
    getPermissions: () => Promise.reject('Unknown method'),
    getIdentity: () =>
        Promise.resolve({
            id: 'user',
            fullName: 'GETTEM',
            // avatar: 'data:image/jpeg;base64,',
        }),
};

export default authProvider;
