import { FC } from 'react';
import {
    Datagrid,
    DateField,
    DateInput,
    Filter,
    FilterProps,
    List,
    ListProps,
    SearchInput,
    TextField,
    downloadCSV,
    InputProps,
} from 'react-admin';
import { makeStyles, Chip } from '@material-ui/core';
import jsonExport from 'jsonexport/dist';
import Pagination from '../common/Pagination';
import ListActions from '../common/ListActions';

const exporter = (data: unknown[]) => {
    jsonExport(data, (err: unknown, csv: unknown) => {
        downloadCSV(['\ufeff', csv], 'orders');
    });
};

const useStyles = makeStyles((theme) => ({
    chip: {
        marginBottom: theme.spacing(1),
    },
}));

const QuickFilter: FC<InputProps> = ({ label }) => {
    const classes = useStyles();
    return <Chip className={classes.chip} label={label} />;
};

const ListFilter = (props: Omit<FilterProps, 'children'>) => (
    <Filter {...props}>
        <SearchInput source="name" alwaysOn />
        <QuickFilter
            label="Order Received"
            source="fulfillmentStatus"
            defaultValue="order_received"
        />
        {/* <QuickFilter label="PO Confirmed" source="fulfillmentStatus" defaultValue="po_confirmed" />
        <QuickFilter label="PO Sent" source="fulfillmentStatus" defaultValue="po_sent" />
        <QuickFilter label="Order Received" source="fulfillmentStatus" defaultValue="order_received" />
        <QuickFilter label="Order Refunded" source="fulfillmentStatus" defaultValue="order_refunded" />
        <QuickFilter label="Order Shipped" source="fulfillmentStatus" defaultValue="order_shipped" />
        <QuickFilter label="Vendor Shipped" source="fulfillmentStatus" defaultValue="vendor_shipped" />
        <QuickFilter label="Order Shipped" source="fulfillmentStatus" defaultValue="order_shipped" />
        <QuickFilter label="Order Cancelled" source="fulfillmentStatus" defaultValue="order_cancelled" /> */}
        <DateInput source="start_date" />
        <DateInput source="end_date" />
    </Filter>
);

const OrderList = (props: ListProps) => {
    const classes = useStyles(props);
    return (
        <List
            {...props}
            classes={classes}
            filters={<ListFilter />}
            pagination={<Pagination />}
            sort={{ field: 'updated_at', order: 'DESC' }}
            exporter={exporter}
            actions={<ListActions {...props} />}
        >
            <Datagrid>
                <TextField source="id" />
                <TextField source="name" label="Title" />
                <TextField source="fulfillment_status" label="Status" />
                <DateField source="created_at" />
                <DateField source="updated_at" />
            </Datagrid>
        </List>
    );
};

export default OrderList;
