import { cloneElement } from 'react';
import {
    ListProps,
    TopToolbar,
    RefreshButton,
    Button,
    ExportButton,
} from 'react-admin';

const ListActions = (props: ListProps) => {
    const { basePath, filters } = props;
    return (
        <TopToolbar>
            {filters &&
                cloneElement(filters, {
                    context: 'button',
                })}
            {basePath === '/balance' && (
                <Button
                    onClick={() => {
                        alert('Deposit Request!');
                    }}
                    label="Deposit Request"
                />
            )}
            <ExportButton icon={<></>} />
            <RefreshButton icon={<></>} />
        </TopToolbar>
    );
};

export default ListActions;
