import { FC } from 'react';
import {
    List,
    ListProps,
    SearchInput,
    Datagrid,
    TextField,
    NumberField,
    Filter,
    FilterProps,
    DateInput,
    downloadCSV,
    InputProps,
} from 'react-admin';
import { makeStyles, Chip } from '@material-ui/core';
import jsonExport from 'jsonexport/dist';
import BalanceShow from './BalanceShow';
import Pagination from '../common/Pagination';
import ListActions from '../common/ListActions';

const exporter = (data: unknown[]) => {
    jsonExport(data, (err: unknown, csv: unknown) => {
        downloadCSV(['\ufeff', csv], 'balances');
    });
};

const useStyles = makeStyles((theme) => ({
    hiddenOnSmallScreens: {
        display: 'table-cell',
        [theme.breakpoints.down('md')]: {
            display: 'none',
        },
    },
    balacneInfo: {
        marginTop: theme.spacing(4),
        marginBottom: theme.spacing(-2),
        color: '#666',
    },
    chip: {
        marginBottom: theme.spacing(1),
    },
}));

const QuickFilter: FC<InputProps> = ({ label }) => {
    const classes = useStyles();
    return <Chip className={classes.chip} label={label} />;
};

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
    <Filter {...props}>
        <SearchInput source="order_reference" alwaysOn />
        {/* <QuickFilter label="Order" source="activity" defaultValue="Order" /> */}
        <QuickFilter label="Refund" source="activity" defaultValue="Refund" />
        {/* <QuickFilter label="Deposit" source="activity" defaultValue="Deposit" /> */}
        {/* <QuickFilter label="Adjust" source="activity" defaultValue="Adjust" /> */}
        <DateInput source="start_date" />
        <DateInput source="end_date" />
    </Filter>
);

const BalanceList = (props: ListProps) => {
    const classes = useStyles(props);
    return (
        <>
            <h3 className={classes.balacneInfo}>Balance $300.00</h3>
            <List
                {...props}
                classes={classes}
                filters={<ListFilters />}
                pagination={<Pagination />}
                sort={{ field: 'created_at', order: 'DESC' }}
                exporter={exporter}
                actions={<ListActions {...props} />}
            >
                <Datagrid rowClick="expand" expand={<BalanceShow />}>
                    <TextField source="id" label="ID" />
                    <TextField
                        source="activity"
                        label="Activities"
                        sortable={false}
                    />
                    <NumberField
                        source="change"
                        locales={['en']}
                        options={{
                            style: 'currency',
                            currency: 'USD',
                        }}
                        label="Amount"
                    />
                    <NumberField
                        source="current"
                        locales={['en']}
                        options={{
                            style: 'currency',
                            currency: 'USD',
                        }}
                        label="Balance"
                    />
                    <TextField source="order_reference" label="Order" />
                    <TextField
                        source="order_line"
                        label="Order Line"
                        sortable={false}
                        headerClassName={classes.hiddenOnSmallScreens}
                        cellClassName={classes.hiddenOnSmallScreens}
                    />
                    <TextField source="created_at" />
                    <TextField
                        source="note"
                        sortable={false}
                        headerClassName={classes.hiddenOnSmallScreens}
                        cellClassName={classes.hiddenOnSmallScreens}
                    />
                </Datagrid>
            </List>
        </>
    );
};

export default BalanceList;
